/*******************************************************************************
 * Styles for the base elements of the theme.
 ******************************************************************************/

//Fonts

@font-face {
  font-family: "calibre";
  src: url("../fonts/Calibre/Calibre-Regular.woff");
  font-display: swap;
}

@font-face {
  font-family: "calibre-light";
  src: url("../fonts/Calibre/Calibre-Light.woff");
  font-display: swap;
}

@font-face {
  font-family: "calibre-semibold";
  src: url("../fonts/Calibre/Calibre-Semibold.woff");
  font-display: swap;
}

@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap');
@import url('https://fonts.googleapis.com/css2?family=JetBrains+Mono:wght@100&display=swap');


// Typography
p, h1, h2, h3, h4, h5, h6, em, div, span, strong {
  color: var(--global-text-color);
  font-family: "calibre-light", sans-serif;
  font-size: 22px;
}

a, table.table a {
  color: var(--global-theme-color);
  font-size: 22px;
  &:hover {
    color: var(--global-theme-color);
    text-decoration: underline;
  }
  &:hover:after {
    width: 100%;
  }
}

.name {
  color: var(--global-text-color-light);
  font-family: "calibre-semibold", sans-serif;
  font-size: 7rem;
}

.name-color {
  color: var(--global-theme-color);
  font-family:'Courier New', Courier, monospace;
  font-size: 20px;
}

.about {
  float: left;
  width: 60%;
  line-height: normal;
}
@media screen and (max-width: 500px)  {
  .about {width: 100%; }
  .name {font-size: 2.5rem;}
  .name-color {font-size: 18px;}
}

::-moz-selection { /* Code for Firefox */
  color: #CCD6F6; /* needs to be set as global for light mode */
  background: #112240;
}

::selection {
  color: #CCD6F6; /* needs to be set as global for light mode */
  background: #112240;
}


// Profile

.profile {
  margin-left: 1rem;
  width: 100%;

  .address {
    margin-bottom: 5px;
    margin-top: 5px;
    font-family: monospace;
    font-size: 1.2rem;
    p {
      display: inline-block;
      margin: 0;
    }
  }
}

@media (min-width: 576px) {
  .profile {
    width: 30%;
    .address {
      p { display: block; }
    }
  }
}

.post-description {
  margin-bottom: 2rem;
  font-size: 0.875rem;
  a {
    color: inherit;
    &:hover {
      color: var(--global-theme-color);
      text-decoration: none;
    }
  }
}


// Navbar customization

.navbar {
  box-shadow: none;
  border-bottom: 0px solid $grey-color-light;
  background-color: var(--global-bg-color);
  opacity: 0.95;
}
.navbar.navbar-light {
  a {
    &:hover {
      text-decoration: none;
    }
  }
  .navbar-brand {
    color: var(--global-text-color);
  }
  .navbar-nav .nav-item .nav-link {
    color: var(--global-text-color);
    &:hover {
      color: var(--global-hover-color);
    }
  }
  .navbar-nav .nav-item.active>.nav-link {
      background-color: inherit;
      font-weight: bolder;
      color: var(--global-theme-color);
      &:hover {
        color: var(--global-hover-color);
      }
  }
  .navbar-brand.social {
    padding-bottom: 0;
    padding-top: 0;
    font-size: 1.7rem;
    a {
      i::before {
        color: var(--global-text-color);
        -webkit-transition: all 0.2s ease-in-out;
      }
      &:hover {
        i::before {
          color: var(--global-theme-color);
        }
      }
    }
  }
}

.navbar-toggler {
  .icon-bar {
    display: block;
    width: 22px;
    height: 2px;
    background-color: var(--global-text-color);
    border-radius: 1px;
    margin-bottom: 4px;
    transition: all 0.2s;
  }
  .top-bar {
    transform: rotate(45deg);
    transform-origin: 10% 10%;
  }
  .middle-bar {
    opacity: 0;
  }
  .bottom-bar {
    transform: rotate(-45deg);
    transform-origin: 10% 90%;
  }
}

.navbar-toggler.collapsed {
  .top-bar {
    transform: rotate(0);
  }
  .middle-bar {
    opacity: 1;
  }
  .bottom-bar {
    transform: rotate(0);
  }
}


// News

.news table td {
  font-size: 1rem;
  color: var(--global-text-color);
}

.news table th {
  color: var(--global-text-color);
}

// Social (bottom)

.social {
  text-align: center;
  .contact-icons {
    font-size: 4rem;
    a {
      i::before {
        color: var(--global-text-color);
        -webkit-transition: all 0.2s ease-in-out;
      }
      &:hover {
        i::before {
          color: var(--global-theme-color);
        }
      }
    }
  }
  .contact-note {
    font-size: 0.8rem;
  }
}


// Footer
footer.fixed-bottom {
  background-color: var(--global-footer-bg-color);
  font-size: 0.75rem;
  .container {
    color: var(--global-footer-text-color);
    padding-top: 9px;
    padding-bottom: 8px;
  }
  a {
    color: var(--global-footer-link-color);
    &:hover {
      color: var(--global-theme-color);
      text-decoration: none;
    }
  }
}

footer.sticky-bottom {
  padding-top: 40px;
  padding-bottom: 40px;
  font-size: 0.9rem;
  text-align: center;
  max-width: 2000px;
}


// Blog

.header-bar {
  border-bottom: 1px transparent $grey-color-light;
  text-align: center;
  padding-top: 2rem;
  padding-bottom: 5rem;
  h1 {
    color: var(--global-theme-color);
    font-size: 10rem;
    font-weight: bold;
  }
}

.post-list {
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
  li {
    border: 1px transparent $my-green;
    list-style: none;
    margin-top: 2rem;
    padding-top: 2rem;
    padding-bottom: 2rem;
    background-color: #112240;
    padding-left: 2rem;
    border-radius: 10px;
    .post-meta {
      color: var(--global-text-color-light);
      font-size: 0.875rem;
      margin-bottom: 0;
    }
    a {
      text-decoration: none;
      font-size: 2rem;
      color: #CCD6F6;
      &:hover {
        color: var(--global-theme-color);
      }
    }
  }
}

.pagination {
  .page-item {
    .page-link {
      color: var(--global-text-color);
      &:hover {
        color: $black-color;
      }
    }
    &.active .page-link {
      background-color: var(--global-theme-color);
      &:hover {
        background-color: var(--global-theme-color);
      }
    }
  }
}


// Distill

.distill {
  a:hover {
    border-bottom-color: var(--global-theme-color);
    text-decoration: none;
  }
}


// Rouge Color Customization
figure.highlight {
  margin: 0 0 1rem;
}

pre {
  color: #CCD6F6;
  background-color: var(--global-code-bg-color);
  border-radius: 6px;
  padding: 6px 12px;
  font-family: 'Roboto Mono', monospace;
  font-size: 1rem;
  pre, code {
    background-color: transparent;
    border-radius: 0;
    padding: 0;
  }
}

code {
  color: #CCD6F6;
  background-color: var(--global-code-bg-color);
  border-radius: 2px;
  padding: 2px 2px;
}


// Transitioning Themes
html.transition,
html.transition *,
html.transition *:before,
html.transition *:after {
  transition: all 750ms !important;
  transition-delay: 0 !important;
}


// Projects

.card {
  background-color: #112240;
}

.card:hover {
  background-color: #233554;
  border: #64ffda solid 1px;
  transition: 1s;
  .card-title {
    color: #64ffda;
    font-family: "calibre-semibold", sans-serif;
  }
}

.card-title {
  color: #ccd6f6;
  font-family: "calibre-semibold", sans-serif;
}

.card-title:hover {
  color: #64ffda;
  font-family: "calibre-semibold", sans-serif;
}

.card-text {
  color: #ccd6f6;
  font-size: 30px;
}

.button-project {
  background-color: transparent; /* Green */
  border: 1px solid #64ffda;
  color: #64ffda;
  font-family: 'Roboto Mono', monospace;
  padding: 1px 17px;
  text-align: justify;
  text-decoration: none;
  display: inline-block;
  font-size: 17px;
  margin: 10px 10px;
  cursor: pointer;
}
.button-project:hover {
  background-color: #64ffda;
  color: black;
}

.button1 {border-radius: 8px;}
.button2 {border-radius: 12px;}


//Portfolio

.roundedCorner
{
  width:350px;height:200px;
  border: solid 1px #555;
  background-color: #eed;
  box-shadow: 10px -10px 5px  rgba(0,0,0,0.6);
  -moz-box-shadow: 10px -10px 5px  rgba(0,0,0,0.6);
  -webkit-box-shadow: 10px -10px 5px  rgba(0,0,0,0.6);
  -o-box-shadow: 10px -10px 5px  rgba(0,0,0,0.6);
  border-radius:25px;
}

.port-title{
  color: var(--global-theme-color);
  font-size: xx-large;
  font-family: "calibre-light", sans-serif;
  font-weight: bold;
}

.portfolio-page-wrapper {
  margin: 15px;
  background-color: var(--global-bg-color);
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1;
  padding-top: 20px;
}

.lang-column {
  background: var(--global-bg-color);
  padding: 15px;
  border-left: 2px dotted #64ffda;
  font-family: 'Roboto Mono', monospace;
  font-size: 18px;
}

.soft-column {
background: var(--global-bg-color);
  padding: 15px;
  border-left: 2px dotted #64ffda;
  font-family: 'Roboto Mono', monospace;
  font-size: 18px;
}

//Fancy button

.tablefont {
  font-size: 22px;
}


// Project Archive

.table-hover tbody tr:hover td {
  background: #112240;
  padding-left: 5px;
}

.leftrad{
  border-radius: 5px 0 0 5px;
}

.rightrad{
  border-radius: 0 5px 5px 0;
}

table {
  padding-left: 10px;
  padding-right: 10px;
}
td {
  //text-align: left;
  text-align: left;
  padding-left: 20px;
}
